<template>
	<div class="nav">
        <a href="#"><img class="logo" src="https://res.cloudinary.com/dkhgru81l/image/upload/v1533381402/38391707_518710195218120_1033799573040005120_n_aquyqy.jpg"></a>
        <div class="right-side">
            <div class="filter-container">
                <i class="help material-icons">help_outline</i>
                <div @click="toggleFilter(0)" v-bind:class="{ 'filter-item--active' : filter0 }" class="filter-item"><img src="data:image/png;base64,"></div>
                <div @click="toggleFilter(1)" v-bind:class="{ 'filter-item--active' : filter1 }" class="filter-item"><img src="data:image/png;base64,"></div>
                <div @click="toggleFilter(2)" v-bind:class="{ 'filter-item--active' : filter2 }" class="filter-item"><img src="data:image/png;base64,"></div>
                <div @click="toggleFilter(3)" v-bind:class="{ 'filter-item--active' : filter3 }" class="filter-item"><img src="data:image/png;base64,"></div>
            </div>
            <input v-model="searchContent" type="text" class="search-bar" placeholder="Search">
        </div>
	</div>
</template>

<script>
// @ is an alias to /src

export default {
	name: 'navigation',
	components: {

    },
    data: function(){
        return {
            searchContent: "",
            filter0: false,
            filter1: false,
            filter2: false,
            filter3: false,
        }
    },
    methods: {
        toggleFilter(filterNumber){
            if (filterNumber == 0){
                this.filter0 ? this.filter0 = false: this.filter0 = true;
            }
            if (filterNumber == 1){
                this.filter1 ? this.filter1 = false: this.filter1 = true;
            }
            if (filterNumber == 2){
                this.filter2 ? this.filter2 = false: this.filter2 = true;
            }
            if (filterNumber == 3){
                this.filter3 ? this.filter3 = false: this.filter3 = true;
            }
        }
    },
    watch : {
        searchContent: function (val){

            if (this.filter0 || this.filter1 || this.filter2 || this.filter3){
                console.log("A filter is now active");
                var gen_string = "http://172.16.6.162:8000/api/search/?q=";
                gen_string += val;
                if (this.filter0){
                    gen_string += " " + this.filter1;
                }
                if (this.filter1){
                    gen_string += " " + this.filter1;
                }
                if (this.filter2){
                    gen_string += " " + this.filter1;
                }
                if (this.filter3){
                    gen_string += " " + this.filter1;
                }
                this.$http.get('http://172.16.6.162:8000/api/search/?q=' + gen_string).then((response) => {
			        this.$store.commit('updateData', response.data);
		        })

            }else{
                if (this.userLocation.lng == null){
                    console.log("Is null");
                    this.$http.get('http://172.16.6.162:8000/api/search/?q=' + val).then((response) => {
                        this.$store.commit('updateData', response.data);
                    })
                }else{ 
                    console.log("Is not null");
                    this.$http.get('http://172.16.6.162:8000/api/search/?q=' + val + '&lat=' + this.userLocation.lat + '&lon' + this.userLocation.lng).then((response) => {
                        this.$store.commit('updateData', response.data);
                    })
                }   
            }

    		// this.$http.get('http://172.16.6.162:8000/api/search/?q=' + val).then((response) => {
			//     this.$store.commit('updateData', response.data);
		    // })
        }
    },
    computed: {
        userLocation (){
			return this.$store.getters.userLocation;
		}
    }
}
</script>
